.ProfilePic {
  border-radius: 50%;
  border: 5px solid;
  border-width: 5px;
  border-color: magenta;
}

.About {
  color: magenta;
  font-family: cursive;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.IITT {
  color: #97222b;

  border-radius: 4px;
}

.FooterComp {
  background-color: cyan;
  border-radius: 10px;
}

.Name {
  color: #0010ec;
}

.Projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: space-evenly;
  justify-content: center;
  /* justify-items: space-around; */
  padding-top: 10px;
}

.Project {
  width: 500px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 5px solid;
  border-color: rgb(248, 70, 105);
  border-radius: 10%;
  background-color: rgb(248, 70, 105);
  margin-top: 10px;
  margin-bottom: 10px;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.2);
}

.Project img {
  border-radius: 10%;
  width: 500px;
  height: 300px;
}

.Project:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

